'use client';

import { useState, useEffect } from 'react';
import { db } from '../config/firebase';
import { collection, getDocs, addDoc, updateDoc, deleteDoc, doc, query, where, setDoc } from 'firebase/firestore';
import { v4 as uuidv4 } from 'uuid';
import { FiPlus, FiEdit2, FiTrash2, FiChevronDown, FiChevronRight, FiSave } from 'react-icons/fi';
import ExpandableCard from '../components/shared/ExpandableCard';
import EditableTable from '../components/shared/EditableTable';
import type { 
  Namespace, 
  NamespaceAccount, 
  NamespaceMethod, 
  NamespaceTemplate,
  KeyValuePair,
  FirebaseMethod
} from '../types/namespace';
import { HttpMethod } from '../types/namespace';
import KeyValueTable from '../components/shared/KeyValueTable';
import TemplateSection from '../components/namespace/TemplateSection';
import TabHeader from '../components/shared/TabHeader';
import TemplateMapping from '../components/namespace/TemplateMapping';

export default function NamespaceManagement() {
  const [namespaces, setNamespaces] = useState<Namespace[]>([]);
  const [accounts, setAccounts] = useState<Record<string, NamespaceAccount[]>>({});
  const [methods, setMethods] = useState<Record<string, NamespaceMethod[]>>({});
  const [expandedId, setExpandedId] = useState<string | null>(null);
  const [expandedAccountId, setExpandedAccountId] = useState<string | null>(null);
  const [expandedMethodId, setExpandedMethodId] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);
  const [templates, setTemplates] = useState<NamespaceTemplate[]>([]);
  const [showTemplateModal, setShowTemplateModal] = useState(false);
  const [activeTab, setActiveTab] = useState('Namespaces');
  const [expandedTemplateId, setExpandedTemplateId] = useState<string | null>(null);
  const [selectedTemplate, setSelectedTemplate] = useState<NamespaceTemplate | null>(null);
  const [showJsonModal, setShowJsonModal] = useState(false);
  const [jsonModalData, setJsonModalData] = useState<{ title: string; data: any } | null>(null);
  const [schemaTestData, setSchemaTestData] = useState<string>('');
  const [schemaValidationErrors, setSchemaValidationErrors] = useState<string[]>([]);

  useEffect(() => {
    loadNamespaces();
    loadTemplates();
  }, []);

  useEffect(() => {
    if (expandedId) {
      loadAccountsAndMethods(expandedId);
    }
  }, [expandedId]);

  const loadNamespaces = async () => {
    try {
      const querySnapshot = await getDocs(collection(db, 'namespace'));
      const data = querySnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      })) as Namespace[];
      setNamespaces(data);
      setLoading(false);
    } catch (error) {
      console.error('Error loading namespaces:', error);
      setLoading(false);
    }
  };

  const loadAccountsAndMethods = async (namespaceId: string) => {
    try {
      // Load accounts
      const accountsQuery = query(collection(db, 'namespace-account'), where('namespace-id', '==', namespaceId));
      const accountsSnapshot = await getDocs(accountsQuery);
      const accountsData = accountsSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      })) as NamespaceAccount[];
      setAccounts(prev => ({ ...prev, [namespaceId]: accountsData }));

      // Load methods
      const methodsQuery = query(collection(db, 'namespace-account-method'), where('namespace-id', '==', namespaceId));
      const methodsSnapshot = await getDocs(methodsQuery);
      const methodsData = methodsSnapshot.docs.map(doc => ({
        "method-id": doc.id,
        ...doc.data()
      })) as NamespaceMethod[];
      setMethods(prev => ({ ...prev, [namespaceId]: methodsData }));
    } catch (error) {
      console.error('Error loading accounts and methods:', error);
    }
  };

  const handleAddNamespace = async () => {
    try {
      const id = uuidv4();
      const newNamespace = {
        id,
        'namespace-name': 'New Namespace',
        'namespace-url': '',
      };
      await setDoc(doc(db, 'namespace', id), newNamespace);
      loadNamespaces();
    } catch (error) {
      console.error('Error adding namespace:', error);
    }
  };

  const handleUpdateNamespace = async (namespaceId: string, key: string, value: string) => {
    try {
      await updateDoc(doc(db, 'namespace', namespaceId), {
        [key]: value
      });
      loadNamespaces();
    } catch (error) {
      console.error('Error updating namespace:', error);
    }
  };

  const handleDeleteNamespace = async (namespaceId: string) => {
    if (!confirm('Are you sure you want to delete this namespace?')) return;
    try {
      await deleteDoc(doc(db, 'namespace', namespaceId));
      loadNamespaces();
    } catch (error) {
      console.error('Error deleting namespace:', error);
    }
  };

  const handleAddAccount = async (namespaceId: string) => {
    try {
      const id = uuidv4();
      const newAccount = {
        id,
        'namespace-id': namespaceId,
        'namespace-account-name': 'New Account',
        'namespace-account-url-override': '',
        'namespace-account-header': []
      };
      await setDoc(doc(db, 'namespace-account', id), newAccount);
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error adding account:', error);
    }
  };

  const handleUpdateAccount = async (accountId: string, namespaceId: string, key: string, value: string) => {
    try {
      await updateDoc(doc(db, 'namespace-account', accountId), {
        [key]: value
      });
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error updating account:', error);
    }
  };

  const handleDeleteAccount = async (accountId: string, namespaceId: string) => {
    if (!confirm('Are you sure you want to delete this account?')) return;
    try {
      await deleteDoc(doc(db, 'namespace-account', accountId));
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error deleting account:', error);
    }
  };

  const handleAddMethod = async (namespaceId: string) => {
    try {
      const methodId = uuidv4();
      const newMethod = {
        id: methodId,
        'method-id': methodId,
        'namespace-account-method-id': methodId,
        'namespace-id': namespaceId,
        'namespace-account-method-name': 'New Method',
        'namespace-account-method-type': HttpMethod.GET,
        'namespace-account-method-url-override': '',
        'namespace-account-method-queryParams': []
      };
      await setDoc(doc(db, 'namespace-account-method', methodId), newMethod);
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error adding method:', error);
    }
  };

  const handleUpdateMethod = async (methodId: string, namespaceId: string, key: string, value: any) => {
    try {
      const methodRef = doc(db, 'namespace-account-method', methodId);
      const updateData: Record<string, any> = { [key]: value };
      
      // Generate schemas from raw response without transformation
      if (key === 'sample-request') {
        updateData['request-schema'] = generateJsonSchema(value);
      }
      if (key === 'sample-response') {
        // Save the complete response object as-is
        updateData['sample-response'] = value;
        updateData['response-schema'] = generateJsonSchema(value);
      }

      await updateDoc(methodRef, updateData);
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error updating method:', error);
    }
  };

  const handleDeleteMethod = async (methodId: string, namespaceId: string) => {
    if (!confirm('Are you sure you want to delete this method?')) return;
    try {
      await deleteDoc(doc(db, 'namespace-account-method', methodId));
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error deleting method:', error);
    }
  };

  const handleDuplicateNamespace = async (namespace: Namespace) => {
    try {
      const { id, ...namespaceData } = namespace;
      await addDoc(collection(db, 'namespace'), {
        ...namespaceData,
        'namespace-id': uuidv4(),
        'namespace-name': `${namespaceData['namespace-name']} (Copy)`
      });
      loadNamespaces();
    } catch (error) {
      console.error('Error duplicating namespace:', error);
    }
  };

  const handleDuplicateAccount = async (account: NamespaceAccount, namespaceId: string) => {
    try {
      const { id, ...accountData } = account;
      await addDoc(collection(db, 'namespace-account'), {
        ...accountData,
        'namespace-account-name': `${accountData['namespace-account-name']} (Copy)`,
        'namespace-account-id': uuidv4()
      });
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error duplicating account:', error);
    }
  };

  const handleDuplicateMethod = async (method: NamespaceMethod, namespaceId: string) => {
    try {
      const newMethodId = uuidv4();
      const methodData: FirebaseMethod = {
        ...method,
        id: newMethodId,
        'method-id': newMethodId,
        'namespace-account-method-id': newMethodId,
        'namespace-account-method-name': `${method['namespace-account-method-name']} (Copy)`,
        'namespace-id': namespaceId,
        'request-schema': method['request-schema'] || generateJsonSchema(method['sample-request']),
        'response-schema': method['response-schema'] || generateJsonSchema(method['sample-response'])
      };

      // Remove old IDs
      delete methodData['method-id'];
      
      await setDoc(doc(db, 'namespace-account-method', newMethodId), methodData);
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error duplicating method:', error);
    }
  };

  const handleUpdateHeaders = async (accountId: string, namespaceId: string, headers: KeyValuePair[]) => {
    try {
      await updateDoc(doc(db, 'namespace-account', accountId), {
        'namespace-account-header': headers
      });
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error updating headers:', error);
    }
  };

  const handleUpdateQueryParams = async (methodId: string, namespaceId: string, params: KeyValuePair[]) => {
    try {
      await updateDoc(doc(db, 'namespace-account-method', methodId), {
        'namespace-account-method-queryParams': params
      });
      loadAccountsAndMethods(namespaceId);
    } catch (error) {
      console.error('Error updating query params:', error);
    }
  };

  const loadTemplates = async () => {
    try {
      const querySnapshot = await getDocs(collection(db, 'namespace-templates'));
      const data = querySnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      })) as NamespaceTemplate[];
      setTemplates(data);
    } catch (error) {
      console.error('Error loading templates:', error);
    }
  };


  const handleApplyTemplate = async (
    template: NamespaceTemplate,
    mapping: { namespaceId?: string, accountMappings: Record<string, string>, methodMappings: Record<string, string> }
  ) => {
    try {
      let namespaceId = mapping.namespaceId;

      // Create new namespace if needed
      if (!namespaceId) {
        namespaceId = uuidv4();
        const namespace = {
          id: namespaceId,
          ...template.template.namespace
        };
        await setDoc(doc(db, 'namespace', namespaceId), namespace);
      }

      // Create or update accounts
      for (const templateAccount of template.template.accounts) {
        const existingAccountId = mapping.accountMappings[templateAccount['namespace-account-name']];
        if (existingAccountId) {
          await updateDoc(doc(db, 'namespace-account', existingAccountId), templateAccount);
        } else {
          const accountId = uuidv4();
          await setDoc(doc(db, 'namespace-account', accountId), {
            id: accountId,
            ...templateAccount,
            'namespace-id': namespaceId
          });
        }
      }

      // Handle methods
      for (const templateMethod of template.template.methods) {
        const existingMethodId = mapping.methodMappings[templateMethod['namespace-account-method-name']];
        const methodData: FirebaseMethod = {
          ...templateMethod,
          'namespace-id': namespaceId,
          'request-schema': generateJsonSchema(templateMethod['sample-request']),
          'response-schema': generateJsonSchema(templateMethod['sample-response'])
        };

        if (existingMethodId) {
          await updateDoc(doc(db, 'namespace-account-method', existingMethodId), methodData);
        } else {
          const methodId = uuidv4();
          await setDoc(doc(db, 'namespace-account-method', methodId), methodData);
        }
      }

      loadNamespaces();
    } catch (error) {
      console.error('Error applying template:', error);
    }
  };

  const setShowMapping = (template: NamespaceTemplate) => {
    setSelectedTemplate(template);
  };

  const showPrettyJson = (title: string, data: any) => {
    setJsonModalData({ title, data }); // Show the complete response object
    setShowJsonModal(true);
  };

  const generateJsonSchema = (data: any): any => {
    if (data === null || data === undefined || data === '') {
      return { type: ['string', 'number', 'boolean'] }; // Allow any primitive type for nullable fields
    }
    
    const type = Array.isArray(data) ? 'array' : typeof data;
    
    switch (type) {
      case 'array':
        if (!data.length) return { type: 'array' };
        
        // Collect all values for each field to determine possible types
        const itemTypes = data.reduce((acc: Record<string, any[]>, item: Record<string, any>) => {
          if (typeof item === 'object' && item !== null) {
            Object.entries(item).forEach(([key, value]) => {
              acc[key] = acc[key] || [];
              acc[key].push(value);
            });
          }
          return acc;
        }, {});

        // Generate schema with possible types for each field
        const properties: Record<string, any> = {};
        Object.entries(itemTypes).forEach(([key, values]) => {
          const types = new Set((values as any[]).map(v => v === null ? 'string' : typeof v));
          properties[key] = { type: Array.from(types) };
        });

        return {
          type: 'array',
          items: {
            type: 'object',
            properties
          }
        };

      case 'object': {
        const properties: Record<string, any> = {};
        
        Object.entries(data).forEach(([key, value]) => {
          properties[key] = generateJsonSchema(value);
        });
        
        return {
          type: 'object',
          properties
        };
      }
      default:
        return { type }; // Return actual type for non-null values
    }
  };

  const validateValue = (value: any, schema: any, path: string = '', errors: string[] = []): void => {
    try {
      // Skip validation for null/empty values
      if (value === null || value === '') return;

      if (Array.isArray(schema.type)) {
        const isValid = schema.type.some((type: string) => {
          switch (type) {
            case 'string':
              return typeof value === 'string';
            case 'number':
              return typeof value === 'number' && !isNaN(value);
            case 'boolean':
              return typeof value === 'boolean';
            case 'integer':
              return Number.isInteger(value);
            default:
              return typeof value === type;
          }
        });

        if (!isValid) {
          errors.push(`${path}: expected one of [${schema.type.join(', ')}], got ${typeof value}`);
        }
        return;
      }

      switch (schema.type) {
        case 'string':
          if (typeof value !== 'string') {
            errors.push(`${path}: expected string, got ${typeof value}`);
          }
          break;
        case 'number':
          if (typeof value !== 'number' || isNaN(value)) {
            errors.push(`${path}: expected number, got ${typeof value}`);
          }
          break;
        case 'boolean':
          if (typeof value !== 'boolean') {
            errors.push(`${path}: expected boolean, got ${typeof value}`);
          }
          break;
        case 'integer':
          if (!Number.isInteger(value)) {
            errors.push(`${path}: expected integer, got ${typeof value}`);
          }
          break;
        case 'object':
          if (typeof value === 'object' && !Array.isArray(value)) {
            Object.entries(schema.properties || {}).forEach(([key, propSchema]: [string, any]) => {
              if (key in value) {
                validateValue(value[key], propSchema, path ? `${path}.${key}` : key, errors);
              }
            });
          } else {
            errors.push(`${path}: expected object, got ${typeof value}`);
          }
          break;
        case 'array':
          if (Array.isArray(value)) {
            value.forEach((item, index) => {
              if (schema.items) {
                validateValue(item, schema.items, `${path}[${index}]`, errors);
              }
            });
          } else {
            errors.push(`${path}: expected array, got ${typeof value}`);
          }
          break;
      }
    } catch (e) {
      errors.push(`Error validating ${path}: ${e}`);
    }
  };

  const validateAgainstSchema = (schema: any, data: any): string[] => {
    const errors: string[] = [];
    try {
      const parsedData = typeof data === 'string' ? JSON.parse(data) : data;
      validateValue(parsedData, schema, '', errors);
    } catch (e) {
      errors.push('Invalid JSON format');
    }
    return errors;
  };

  return (
    <div className="p-6">
      <TabHeader 
        tabs={['Namespaces', 'Templates']}
        activeTab={activeTab}
        onTabChange={setActiveTab}
      />

      {activeTab === 'Namespaces' ? (
        // Namespaces View
        <div>
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-2xl font-bold">Namespace Management</h1>
            <button
              onClick={() => handleAddNamespace()}
              className="px-4 py-2 bg-[#ff6b4a] text-white rounded flex items-center gap-2"
            >
              <FiPlus /> Add Namespace
            </button>
          </div>

          <div className="space-y-4">
            {namespaces.map((namespace) => (
              <ExpandableCard
                key={namespace.id}
                title={namespace['namespace-name']}
                subtitle={namespace['namespace-url']}
                isExpanded={expandedId === namespace.id}
                onToggle={() => setExpandedId(expandedId === namespace.id ? null : namespace.id)}
                onDelete={() => handleDeleteNamespace(namespace.id)}
                onDuplicate={() => handleDuplicateNamespace(namespace)}
                // actions={
                //   // <button
                //   //   onClick={() => handleCreateTemplate(namespace)}
                //   //   className="text-sm text-blue-600 hover:text-blue-800 flex items-center gap-1"
                //   // >
                //   //   <FiSave /> Create Template
                //   // </button>
                // }
              >
                <div className="space-y-6">
                  {/* Namespace Details */}
                  <div>
                    <h3 className="font-medium mb-2">Namespace Details</h3>
                    <EditableTable
                      data={{
                        ...namespace,
                        'namespace-id': namespace.id
                      }}
                      onUpdate={(key, value) => handleUpdateNamespace(namespace.id, key, value)}
                      excludeKeys={[]}
                      readOnlyKeys={['id', 'namespace-id']}
                    />
                  </div>

                  {/* Accounts Section */}
                  <div>
                    <div className="flex justify-between items-center mb-2">
                      <h3 className="font-medium">Accounts</h3>
                      <button
                        onClick={() => handleAddAccount(namespace.id)}
                        className="text-sm text-[#ff6b4a] hover:text-[#ff5436] flex items-center gap-1"
                      >
                        <FiPlus /> Add Account
                      </button>
                    </div>
                    <div className="space-y-2">
                      {accounts[namespace.id]?.map((account) => (
                        <ExpandableCard
                          key={account.id}
                          title={account['namespace-account-name']}
                          subtitle={account['namespace-account-url-override']}
                          isExpanded={expandedAccountId === account.id}
                          onToggle={() => setExpandedAccountId(expandedAccountId === account.id ? null : account.id)}
                          onDelete={() => handleDeleteAccount(account.id, namespace.id)}
                          onDuplicate={() => handleDuplicateAccount(account, namespace.id)}
                        >
                          <EditableTable
                            data={account}
                            onUpdate={(key, value) => handleUpdateAccount(account.id, namespace.id, key, value)}
                            excludeKeys={['id', 'namespace-account-header']}
                            readOnlyKeys={['namespace-id']}
                          />
                          <KeyValueTable
                            data={account['namespace-account-header']}
                            onUpdate={(headers) => handleUpdateHeaders(account.id, namespace.id, headers)}
                            title="Headers"
                            allowJsonValue={false}
                          />
                        </ExpandableCard>
                      ))}
                    </div>
                  </div>

                  {/* Methods Section */}
                  <div>
                    <div className="flex justify-between items-center mb-2">
                      <h3 className="font-medium">Methods</h3>
                      <button
                        onClick={() => handleAddMethod(namespace.id)}
                        className="text-sm text-[#ff6b4a] hover:text-[#ff5436] flex items-center gap-1"
                      >
                        <FiPlus /> Add Method
                      </button>
                    </div>
                    <div className="space-y-2">
                      {methods[namespace.id]?.map((method) => (
                        <ExpandableCard
                          key={method['method-id']}
                          title={method['namespace-account-method-name']}
                          subtitle={method['namespace-account-method-url-override']}
                          isExpanded={expandedMethodId === method['method-id']}
                          onToggle={() => setExpandedMethodId(
                            expandedMethodId === method['method-id'] ? null : (method['method-id'] || null)
                          )}
                          onDelete={() => method['method-id'] && handleDeleteMethod(method['method-id'], namespace.id)}
                          onDuplicate={() => handleDuplicateMethod(method, namespace.id)}
                        >
                          <div className="grid grid-cols-2 gap-4 mb-4">
                            <div>
                              <label className="text-sm font-medium text-gray-600">Sample Request</label>
                              <div className="mt-1 flex items-center gap-2">
                                <span className="text-gray-500 text-sm">[Sample Data Available]</span>
                                <button
                                  onClick={() => showPrettyJson('Sample Request', method['sample-request'])}
                                  className="text-blue-600 hover:text-blue-800 text-sm"
                                >
                                  View JSON
                                </button>
                                <button
                                  onClick={() => showPrettyJson('Request Schema', method['request-schema'] || generateJsonSchema(method['sample-request']))}
                                  className="text-blue-600 hover:text-blue-800 text-sm"
                                >
                                  View Schema
                                </button>
                              </div>
                            </div>
                            <div>
                              <label className="text-sm font-medium text-gray-600">Sample Response</label>
                              <div className="mt-1 flex items-center gap-2">
                                <span className="text-gray-500 text-sm">[Sample Data Available]</span>
                                <button
                                  onClick={() => showPrettyJson('Sample Response', method['sample-response'])}
                                  className="text-blue-600 hover:text-blue-800 text-sm"
                                >
                                  View JSON
                                </button>
                                <button
                                  onClick={() => showPrettyJson('Response Schema', generateJsonSchema(method['sample-response']))}
                                  className="text-blue-600 hover:text-blue-800 text-sm"
                                >
                                  View Schema
                                </button>
                              </div>
                            </div>
                          </div>
                          <EditableTable
                            data={method}
                            onUpdate={(key, value) => handleUpdateMethod(method['method-id'], namespace.id, key, value)}
                            excludeKeys={['namespace-account-method-queryParams']}
                            readOnlyKeys={['namespace-id', 'method-id']}
                            enumFields={{
                              'namespace-account-method-type': HttpMethod
                            }}
                          />
                          <KeyValueTable
                            data={method['namespace-account-method-queryParams'] || []}
                            onUpdate={(params) => handleUpdateQueryParams(method['method-id'], namespace.id, params)}
                            title="Query Parameters"
                            allowJsonValue={false}
                          />
                        </ExpandableCard>
                      ))}
                    </div>
                  </div>
                </div>
              </ExpandableCard>
            ))}
          </div>
        </div>
      ) : (
        // Templates View
        <div>
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-2xl font-bold">Template Management</h1>
          </div>

          <div className="space-y-4">
            {templates.map((template) => (
              <ExpandableCard
                key={template.id}
                title={template.name}
                subtitle={template.template.namespace['namespace-url']}
                isExpanded={expandedTemplateId === template.id}
                onToggle={() => setExpandedTemplateId(expandedTemplateId === template.id ? null : template.id)}
                onApply={async () => setShowMapping(template)}
              >
                <div className="space-y-4">
                  {/* Namespace Section */}
                  <div>
                    <h3 className="font-medium mb-2">Namespace</h3>
                    <EditableTable
                      data={template.template.namespace}
                      onUpdate={async () => {}}
                      readOnly={true}
                    />
                  </div>

                  {/* Accounts Section */}
                  <div>
                    <h3 className="font-medium mb-2">Accounts</h3>
                    {template.template.accounts.map((account, index) => (
                      <div key={index} className="mb-4">
                        <EditableTable
                          data={account}
                          onUpdate={async () => {}}
                          readOnly={true}
                        />
                        <KeyValueTable
                          data={account['namespace-account-header']}
                          onUpdate={async () => {}}
                          title="Headers"
                          readOnly={true}
                        />
                      </div>
                    ))}
                  </div>

                  {/* Methods Section */}
                  <div>
                    <h3 className="font-medium mb-2">Methods</h3>
                    {template.template.methods.map((method, index) => (
                      <div key={index} className="mb-4">
                        <div className="grid grid-cols-2 gap-4 mb-4">
                          <div>
                            <label className="text-sm font-medium text-gray-600">Sample Request</label>
                            <div className="mt-1 flex items-center gap-2">
                              <span className="text-gray-500 text-sm">[Sample Data Available]</span>
                              <button
                                onClick={() => showPrettyJson('Sample Request', method['sample-request'])}
                                className="text-blue-600 hover:text-blue-800 text-sm"
                              >
                                View JSON
                              </button>
                              <button
                                onClick={() => showPrettyJson('Request Schema', method['request-schema'] || generateJsonSchema(method['sample-request']))}
                                className="text-blue-600 hover:text-blue-800 text-sm"
                              >
                                View Schema
                              </button>
                            </div>
                          </div>
                          <div>
                            <label className="text-sm font-medium text-gray-600">Sample Response</label>
                            <div className="mt-1 flex items-center gap-2">
                              <span className="text-gray-500 text-sm">[Sample Data Available]</span>
                              <button
                                onClick={() => showPrettyJson('Sample Response', method['sample-response'])}
                                className="text-blue-600 hover:text-blue-800 text-sm"
                              >
                                View JSON
                              </button>
                              <button
                                onClick={() => showPrettyJson('Response Schema', generateJsonSchema(method['sample-response']))}
                                className="text-blue-600 hover:text-blue-800 text-sm"
                              >
                                View Schema
                              </button>
                            </div>
                          </div>
                        </div>
                        <EditableTable
                          data={method}
                          onUpdate={async () => {}}
                          readOnly={true}
                        />
                        <KeyValueTable
                          data={method['namespace-account-method-queryParams'] || []}
                          onUpdate={async () => {}}
                          title="Query Parameters"
                          readOnly={true}
                        />
                      </div>
                    ))}
                  </div>
                </div>
              </ExpandableCard>
            ))}
          </div>
        </div>
      )}

      {/* Add Template Mapping Modal */}
      {selectedTemplate && (
        <TemplateMapping
          template={selectedTemplate}
          existingNamespaces={namespaces}
          existingAccounts={Object.values(accounts).flat()}
          existingMethods={Object.values(methods).flat()}
          onMapComplete={handleApplyTemplate}
        />
      )}

      {/* JSON Preview Modal */}
      {showJsonModal && jsonModalData && (
        <div className="fixed inset-0 bg-black bg-opacity-30 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl w-[800px] max-h-[80vh] overflow-hidden">
            <div className="px-4 py-3 border-b flex justify-between items-center">
              <h3 className="font-medium">{jsonModalData.title}</h3>
              <button
                onClick={() => setShowJsonModal(false)}
                className="text-gray-500 hover:text-gray-700"
              >
                
              </button>
            </div>
            
            {jsonModalData.title.includes('Schema') ? (
              <div className="grid grid-rows-2 gap-4 h-[calc(80vh-60px)]">
                {/* Schema Viewer */}
                <div className="p-4 overflow-auto border-b">
                  <h4 className="font-medium mb-2">JSON Schema</h4>
                  <pre className="bg-gray-50 p-4 rounded text-sm">
                    {JSON.stringify(jsonModalData.data, null, 2)}
                  </pre>
                </div>
                
                {/* Schema Validator */}
                <div className="p-4">
                  <h4 className="font-medium mb-2">Validate JSON</h4>
                  <div className="space-y-4">
                    <textarea
                      value={schemaTestData}
                      onChange={(e) => {
                        setSchemaTestData(e.target.value);
                        setSchemaValidationErrors(validateAgainstSchema(jsonModalData.data, e.target.value));
                      }}
                      placeholder={`Example valid JSON:
{
  "orders": [
    {
      "note": null,
      "taxes_included": false,
      "confirmed": true
    }
  ]
}`}
                      className="w-full h-32 p-2 border rounded font-mono text-sm"
                    />
                    
                    {schemaValidationErrors.length > 0 && (
                      <div className="bg-red-50 p-3 rounded">
                        <h5 className="text-red-700 font-medium mb-1">Validation Errors:</h5>
                        <ul className="text-red-600 text-sm list-disc list-inside">
                          {schemaValidationErrors.map((error, i) => (
                            <li key={i}>{error}</li>
                          ))}
                        </ul>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            ) : (
              // Regular JSON viewer
              <div className="p-4 overflow-auto max-h-[calc(80vh-60px)]">
                <pre className="bg-gray-50 p-4 rounded text-sm">
                  {jsonModalData.data ? JSON.stringify(jsonModalData.data, null, 2) : 'No data available'}
                </pre>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
} 